
import photo from '../../../static/userphoto.jpeg';
import XHeader from 'vux/src/components/x-header/index.vue'


export default {
name: 'page',
components: {
XHeader,

},
data() {
return {
title: "",
type: '1',
defaultImg: 'this.src="' + require('../../assets/userphoto.jpeg') + '"',
prolist: [{
title: '',
time: "",
list: [{
label: '',
value: ''
}, {
label: '',
value: ''
}, {
label: '',
value: '20W'
}, {
label: '',
value: '2018-08-11'
}]
}],
uid: 0
}
},
created() {
this.uid = this.util.getUid();

if(this.uid == null || this.uid == undefined) {
this.uid = 33;
}
this.onLoadPro();
},
methods: { //
onLoadPro: function() {
let params = {
uid: this.uid
}
console.log(params);
this.$axios.post(this.umcons.serviceAddress.GET_CUSTOMER, params).then(function(response) {
var error = response.data.error;
if(error == 0) {
this.prolist = response.data.data;

} else {
this.util.showAlert("");
}

}.bind(this));
},
getInfo: function(msg) {
if(msg != undefined) {
return msg;
} else {
return ""
}
},
getPhoto: function(msg) {
if(msg != undefined) {
return msg;
} else {
return photo;
}
},openClientFlow:function(id){
this.$router.push({
name: 'AddClient',
params: {
uid: id
}
});
}
}
}



